import AVATAR from "client/assets/images/icons/male.png";
import { UserService } from "client/services";
import { CountryCity } from "common";
import moment from "moment";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { Link, useNavigate } from "react-router-dom";
import { adminMenuItems } from "./menuItems";
import { AuthService } from "client/services";

export const DashboardSidebar = () => {
  const [t, i18nextLng] = useTranslation();
  const pathname = window.location.pathname;
  const [currentUser, setCurrentUser] = useState(null);
  const navigate = useNavigate();
  const [isLoggedIn, setIsLoggedIn] = useState();

  const lang = "en";
  useEffect(() => {
    const fetchCurrentUser = async () => {
      try {
        const response = await UserService.getCurrentUser();
        setCurrentUser(response?.body);
      } catch (error) {
        console.log("Error fetching current user:", error);
      }
    };
    fetchCurrentUser();
  }, []);

  const renderMenuItem = (item, index) => {
    const shouldRenderItem =
      item.role === "all" ||
      (currentUser?.roles?.some((role) => role?.name === "ROLE_ADMIN") &&
        item?.role === "ROLE_ADMIN");
    if (!shouldRenderItem) {
      return null;
    }

    return (
      <li key={index} className={pathname.includes(item.path) ? "active" : ""}>
        <Link
          to={item.path}
          onClick={(e) => {
            if (item.onClick) {
              e.preventDefault(); // Prevent default link behavior if there's an onClick
              item.onClick(); // Call the function if it exists
            }
          }}
        >
          <i className={item.icon}></i>
          <span>{item.label}</span>
        </Link>
      </li>
    );
  };

  const customActions = {
    logout: () => {
      console.log("Logging out...");
      AuthService.logout();
      setIsLoggedIn(false);
      navigate("/");
    },
    // Add other custom actions as needed
  };

  return (
    <div className="profile-sidebar">
      <div className="widget-profile pro-widget-content">
        <div className="profile-info-widget">
          <a href="#0" className="booking-doc-img">
            <img src={AVATAR} alt="User" />
          </a>
          <div className="profile-det-info">
            <h3>{currentUser?.email}</h3>
            <div className="patient-details">
              <h5>
                <i className="fas fa-birthday-cake"></i>{" "}
                {currentUser?.dateOfBirth &&
                  moment(currentUser?.dateOfBirth).format("DD MMM YYYY")}
                ,{" "}
                {currentUser?.dateOfBirth &&
                  moment(currentUser?.dateOfBirth, "YYYY-MM-DD")
                    .month(0)
                    .from(moment().month(0))}
              </h5>
              <h5 className="mb-0">
                <i className="fas fa-map-marker-alt"></i>
                {currentUser?.cityOfResidence &&
                  currentUser?.cityOfResidence},{" "}
                {currentUser?.countryOfResidence &&
                  CountryCity.getCountryOfCode(currentUser?.countryOfResidence)
                    .name}
              </h5>
            </div>
          </div>
        </div>
      </div>
      <div className="dashboard-widget">
        <nav className="dashboard-menu">
          <ul>{adminMenuItems(t, customActions).map(renderMenuItem)}</ul>
        </nav>
      </div>
    </div>
  );
};
export default DashboardSidebar;
