import { APIRequest } from "common";

const User_API_URL = `${process.env.REACT_APP_YMP_API}/api/v1/user`;

const getAllUsers = (page, size) => {
  const url = `${User_API_URL}?page=${page}&size=${size}`;
  return APIRequest({
    url: url,
    method: "get",
  });
};

const getUserByID = (userId) => {
  const url = `${User_API_URL}/id/${userId}`;
  return APIRequest({
    url: url,
    method: "get",
  });
};

const getUserByEmail = (email) => {
  const url = `${User_API_URL}/email/${email}`;
  return APIRequest({
    url: url,
    method: "get",
  });
};

const createUser = (userData) => {
  const url = User_API_URL;
  return APIRequest({
    url: url,
    method: "post",
    requestBody: { ...userData },
  });
};

const updateUser = (userID, userData) => {
  const url = `${User_API_URL}/${userID}`;
  return APIRequest({
    url: url,
    method: "put",
    requestBody: { ...userData },
  });
};

const deleteUser = (userID) => {
  const url = `${User_API_URL}/${userID}`;
  return APIRequest({
    url: url,
    method: "delete",
  });
};

const UserService = {
  getAllUsers,
  getUserByID,
  createUser,
  updateUser,
  deleteUser,
  getUserByEmail,
};

export default UserService;
