import { APIRequest } from "common";

const Doctor_API_URL = `${process.env.REACT_APP_YMP_API}/api/v1/doctor`;

const getAllDoctors = (page, size) => {
  const url = `${Doctor_API_URL}?page=${page}&size=${size}`;
  return APIRequest({
    url: url,
    method: "get",
  });
};

const getDoctorByID = (doctorId) => {
  const url = `${Doctor_API_URL}/${doctorId}`;
  return APIRequest({
    url: url,
    method: "get",
  });
};

const createDoctor = (doctorData) => {
  const url = Doctor_API_URL;
  return APIRequest({
    url: url,
    method: "post",
    requestBody: { ...doctorData },
  });
};

const updateDoctor = (doctorID, doctorData) => {
  const url = `${Doctor_API_URL}/${doctorID}`;
  return APIRequest({
    url: url,
    method: "put",
    requestBody: { ...doctorData },
  });
};

const deleteDoctor = (doctorID) => {
  const url = `${Doctor_API_URL}/${doctorID}`;
  return APIRequest({
    url: url,
    method: "delete",
  });
};

const DoctorService = {
  getAllDoctors,
  getDoctorByID,
  createDoctor,
  updateDoctor,
  deleteDoctor,
};

export default DoctorService;
