import { APIRequest } from "common";

const Country_API_URL = `${process.env.REACT_APP_YMP_API}/api/v1/country`;

const getAllCountries = () => {
  const url = Country_API_URL;
  return APIRequest({
    url: url,
    method: "get",
  });
};

const getCountryByID = (countryId) => {
  const url = `${Country_API_URL}/${countryId}`;
  return APIRequest({
    url: url,
    method: "get",
  });
};

const createCountry = (data) => {
  const url = Country_API_URL;
  return APIRequest({
    url: url,
    method: "post",
    requestBody: data,
  });
};

const updateCountry = (countryID, data) => {
  const url = `${Country_API_URL}/${countryID}`;
  return APIRequest({
    url: url,
    method: "put",
    requestBody: data,
  });
};

const deleteCountry = (countryID) => {
  const url = `${Country_API_URL}/${countryID}`;
  return APIRequest({
    url: url,
    method: "delete",
  });
};

const CountryService = {
  getAllCountries,
  getCountryByID,
  createCountry,
  updateCountry,
  deleteCountry,
};

export default CountryService;
