import { APIRequest } from "common";

const City_API_URL = `${process.env.REACT_APP_YMP_API}/api/v1/city`;

const getAllCities = () => {
  const url = City_API_URL;
  return APIRequest({
    url: url,
    method: "get",
  });
};

const getCityByID = (CityId) => {
  const url = `${City_API_URL}/${CityId}`;
  return APIRequest({
    url: url,
    method: "get",
  });
};

const createCity = (data) => {
  const url = City_API_URL;
  return APIRequest({
    url: url,
    method: "post",
    requestBody: data,
  });
};

const updateCity = (CityID, data) => {
  const url = `${City_API_URL}/${CityID}`;
  return APIRequest({
    url: url,
    method: "put",
    requestBody: data,
  });
};

const deleteCity = (CityID) => {
  const url = `${City_API_URL}/${CityID}`;
  return APIRequest({
    url: url,
    method: "delete",
  });
};

const CityService = {
  getAllCities,
  getCityByID,
  createCity,
  updateCity,
  deleteCity,
};

export default CityService;
