import { APIRequest } from "common";

const Speciality_API_URL = `${process.env.REACT_APP_YMP_API}/api/v1/speciality`;

const getAllSpecialitiesWithPagination = (page, size) => {
  const url = `${Speciality_API_URL}?page=${page}&size=${size}`;
  return APIRequest({
    url: url,
    method: "get",
  });
};
const getAllSpecialities = () => {
  const url = `${Speciality_API_URL}`;
  return APIRequest({
    url: url,
    method: "get",
  });
};
const getSpecialityByID = (specialityId) => {
  const url = `${Speciality_API_URL}/${specialityId}`;
  return APIRequest({
    url: url,
    method: "get",
  });
};

const createSpeciality = (specialityData) => {
  const url = Speciality_API_URL;
  return APIRequest({
    url: url,
    method: "post",
    requestBody: { ...specialityData },
  });
};

const updateSpeciality = (specialityID, specialityData) => {
  const url = `${Speciality_API_URL}/${specialityID}`;
  return APIRequest({
    url: url,
    method: "put",
    requestBody: { ...specialityData },
  });
};

const deleteSpeciality = (specialityID) => {
  const url = `${Speciality_API_URL}/${specialityID}`;
  return APIRequest({
    url: url,
    method: "delete",
  });
};

const SpecialityService = {
  getAllSpecialitiesWithPagination,
  getAllSpecialities,
  getSpecialityByID,
  createSpeciality,
  updateSpeciality,
  deleteSpeciality,
};

export default SpecialityService;
