import React, { useState, useEffect } from "react";
import { Modal } from "react-bootstrap";
import StickyBox from "react-sticky-box";
import { useTranslation } from "react-i18next";
import Alert from "@mui/material/Alert";
import CircularProgress from "@mui/material/CircularProgress";
import { SpecialityService, DoctorService } from "admin/services";
import { AuthService } from "client/services"
import AddUpdateForm from "./AddUpdateForm";
import { DataTable } from "common/components";
import { PageMode } from "common";
import DashboardSidebar from "client/components/dashboard/sidebar/sidebar.jsx";

function DoctorsList() {
  const [t, i18n] = useTranslation();
  const [loading, setLoading] = useState(false);
  const [deleting, setDeleting] = useState(false);
  const [show, setShow] = useState(false);
  const [showDelete, setShowDelete] = useState(false);
  const [showSuccess, setShowSuccess] = useState(false);
  const [message, setMessage] = useState();
  const [messageDelete, setMessageDelete] = useState();
  const [doctorsData, setDoctorsData] = useState();
  const [selectedDoctor, setSelectedDoctor] = useState(null);
  const [page, setPage] = useState(1);
  const [dataPage, setDataPage] = useState();
  const [pageIndex, setPageIndex] = useState(0);
  const [mode, setMode] = useState(PageMode.Add);
  const handleClose = () => setShow(false);
  const [specialitiesData, setSpecialitiesData] = useState();
  const handleShow = () => {
    setShow(true);
    setShowSuccess(false);
  };
  const handleCloseDelete = () => setShowDelete(false);
  const handleShowDelete = () => {
    setShowDelete(true);
    setShowSuccess(false);
  };

  useEffect(async () => {
    const uurrentUser = AuthService.getCurrentUser();
    if (uurrentUser) {
    console.log("ðŸš€ ~ file: index.jsx ~ line 44 ~ useEffect ~ uurrentUser", uurrentUser)
    
    }
    setLoading(true);
    const response = await DoctorService.getAllDoctors(pageIndex, 10);
    setMessage(!!response?.message && response?.message);
    if (response.success) {
      const responseSpecialities = await SpecialityService.getAllSpecialities();
      setMessage(
        !!responseSpecialities?.message && responseSpecialities?.message
      );
      if (responseSpecialities.success) {
        setSpecialitiesData(responseSpecialities.body);
      }

      const doctors = response?.body?.map((doctor) => {
        return {
          ...doctor,
          name: `${doctor.firstName ? doctor.firstName : ""}${
            doctor.lastName ? " " + doctor.lastName : ""
          }`,
          specialityId: specialitiesData?.find(
            (s) => s.id === doctor.specialityId
          )?.name,
        };
      });

      setDoctorsData(doctors);
      setDataPage({ ...response?.pagingation });
    }

    setLoading(false);
  }, [pageIndex]);

  const handlePageChange = (event, value) => {
    setPageIndex(value - 1);
    setPage(value);
    setShowSuccess(false);
  };

  const deleteDoctorHandler = async (id) => {
    setDeleting(true);

    const response = await DoctorService.deleteDoctor(id);

    setDeleting(false);
    if (!!!response.success && !response.success) {
      setMessageDelete(!!response.message && response.message);
      return;
    }
    handleCloseDelete();
    if (doctorsData?.length === 1) {
      setPageIndex(pageIndex - 1);
    }

    setDoctorsData(doctorsData.filter((u) => u.id != id));
  };

  const columns = [
    { id: "title", label: "Title" },
    { id: "name", label: "Name" },
    { id: "email", label: "Email" },
    { id: "specialityId", label: "Speciality" },
    { id: "phone", label: "Phone", className: "text-center" },
  ];
  let tableContent = (
    <DataTable
      columns={columns}
      rows={doctorsData}
      onPageChange={handlePageChange}
      totalPages={dataPage?.totalPages}
      page={page}
      showActions={true}
      onEdit={(doctor) => {
        handleShow();
        setSelectedDoctor(doctor);
        setMode(PageMode.Update);
      }}
      onDelete={(doctor) => {
        handleShowDelete();
        setSelectedDoctor(doctor);
        setMessageDelete(null);
      }}
    />
  );

  return (
    <div>
      <div className="content">
        <div className="container-fluid">
          <div className="row">
            <div className="col-md-5 col-lg-4 col-xl-3 theiaStickySidebar">
              <StickyBox offsetTop={20} offsetBottom={20}>
                <DashboardSidebar />
              </StickyBox>
            </div>
            <div className="col-md-7 col-lg-8 col-xl-9">
              <div className="row">
                <div className="col-sm-12">
                  <div className="card">
                    <div className="card-header">
                      <h4 className="card-title float-left">Doctors List</h4>
                      <button
                        className="btn btn-primary float-right"
                        onClick={() => {
                          handleShow();
                          setSelectedDoctor(null);
                          setMode(PageMode.Add);
                        }}
                        data-toggle="modal"
                      >
                        Add
                      </button>
                    </div>
                    <div className="card-body ">
                      <div className="card card-table mb-0">
                        <div className="card-body">
                          <div className="table-responsive">
                            {message?.map((m, i) => (
                              <div
                                key={i}
                                className="invalid-feedback d-block mb-2"
                              >
                                {m}
                              </div>
                            ))}
                            {loading ? (
                              <div className="centered">
                                <h4>
                                  <div>
                                    <CircularProgress />
                                  </div>
                                  <div>
                                    <span style={{ color: "#15558d" }}>
                                      {t("GettingDoctors")}
                                    </span>
                                  </div>
                                </h4>
                              </div>
                            ) : (
                              <>
                                {showSuccess ? (
                                  <div style={{ margin: "20px" }}>
                                    <Alert severity="success">{`Doctor have been ${mode} successfully!`}</Alert>
                                  </div>
                                ) : (
                                  <></>
                                )}
                                {tableContent}
                              </>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Modal
        show={show}
        onHide={handleClose}
        size="lg"
        scrollable={true}
        aria-labelledby="example-modal-sizes-title-sm"
      >
        <Modal.Header>
          <h5 className="modal-title">{`${mode} Doctor`}</h5>
          <button
            type="button"
            className="close"
            data-dismiss="modal"
            aria-label="Close"
            onClick={handleClose}
          >
            <span aria-hidden="true">Ã—</span>
          </button>
        </Modal.Header>
        <Modal.Body>
          <div className="modal-body">
            <div>
              <AddUpdateForm
                selectedDoctor={selectedDoctor}
                mode={mode}
                onClose={(doctor) => {
                  handleClose();
                  if (doctor) {
                    if (mode === PageMode.Update) {
                      const updatedDoctors = doctorsData.map((u) => {
                        return u.id === doctor.id
                          ? {
                              ...doctor,
                              name: `${
                                doctor.firstName ? doctor.firstName : ""
                              }${doctor.lastName ? " " + doctor.lastName : ""}`,
                              specialityId: specialitiesData?.find(
                                (s) => s.id === u.specialityId
                              )?.name,
                            }
                          : u;
                      });
                      setDoctorsData(updatedDoctors);
                    } else {
                      const addedDoctor = {
                        ...doctor,
                        name: `${doctor.firstName ? doctor.firstName : ""}${
                          doctor.lastName ? " " + doctor.lastName : ""
                        }`,
                        specialityId: specialitiesData?.find(
                          (s) => s.id === doctor.specialityId
                        )?.name,
                      };
                      if (doctorsData?.length === 10) {
                        setDataPage({
                          ...dataPage,
                          totalPages: dataPage.totalPages + 1,
                        });
                      }
                      setDoctorsData([...doctorsData, addedDoctor]);
                    }
                    setShowSuccess(true);
                  }
                }}
              />
            </div>
          </div>
        </Modal.Body>
      </Modal>

      <Modal
        show={showDelete}
        onHide={handleCloseDelete}
        aria-labelledby="example-modal-sizes-title-sm"
        centered
      >
        <Modal.Header>
          <h5 className="modal-title">Delete Doctor Confirmation</h5>
          <button
            type="button"
            className="close"
            data-dismiss="modal"
            aria-label="Close"
            onClick={handleCloseDelete}
          >
            <span aria-hidden="true">Ã—</span>
          </button>
        </Modal.Header>
        <Modal.Body>
          <div className="modal-body">
            {messageDelete && (
              <div className="invalid-feedback d-block mb-2">
                {messageDelete}
              </div>
            )}
            <h5 style={{ color: "red" }}>
              Are you sure you want to delete the following doctor?
            </h5>
            <div>
              <b>Doctor Name:</b>
              {` ${
                selectedDoctor?.firstName ? selectedDoctor?.firstName : ""
              } ${selectedDoctor?.lastName ? selectedDoctor?.lastName : ""}`}
            </div>
            <div>
              <b>Doctor Email:</b>
              {` ${selectedDoctor?.email}`}
            </div>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button
            className="btn btn-danger submit-btn"
            disabled={deleting}
            onClick={() => {
              deleteDoctorHandler(selectedDoctor.id);
            }}
          >
            {!deleting ? `Delete` : ""}
            {deleting && (
              <span className="spinner-border spinner-border-sm mb-1"></span>
            )}
          </button>
          <button
            className="btn btn-secondary submit-btn"
            onClick={handleCloseDelete}
          >
            Close
          </button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}
export default DoctorsList;
