import { APIRequest } from "common";

const Facility_API_URL = `${process.env.REACT_APP_YMP_API}/api/v1/facility`;

const getAllFacilities = () => {
  const url = Facility_API_URL;
  return APIRequest({
    url: url,
    method: "get",
  });
};

const getFacilityByID = (FacilityId) => {
  const url = `${Facility_API_URL}/${FacilityId}`;
  return APIRequest({
    url: url,
    method: "get",
  });
};

const createFacility = (data) => {
  const url = Facility_API_URL;
  return APIRequest({
    url: url,
    method: "post",
    requestBody: data,
  });
};

const updateFacility = (FacilityID, data) => {
  const url = `${Facility_API_URL}/${FacilityID}`;
  return APIRequest({
    url: url,
    method: "put",
    requestBody: data,
  });
};

const deleteFacility = (FacilityID) => {
  const url = `${Facility_API_URL}/${FacilityID}`;
  return APIRequest({
    url: url,
    method: "delete",
  });
};

const FacilityService = {
  getAllFacilities,
  getFacilityByID,
  createFacility,
  updateFacility,
  deleteFacility,
};

export default FacilityService;
