import React, { useState, useMemo, useEffect } from "react";
import { Field, Form } from "react-final-form";
import { InputField, Error } from "common/components";
import { required, PageMode } from "common";
import { SpecialityService, DoctorService } from "admin/services";

function AddUpdateForm(props) {
  const [isLoading, setIsLoading] = useState(false);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState();
  const [specialitiesData, setSpecialitiesData] = useState();
  const [file, setFile] = useState(null);
  const sleep = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

  useEffect(async () => {
    setLoading(true);
    const response = await SpecialityService.getAllSpecialities();
    setMessage(!!response?.message && response?.message);
    if (response.success) {
      setSpecialitiesData(response.body);
    }

    setLoading(false);
  }, []);

  const onSubmit = async (values) => {
    console.log(`onSubmit :: ${JSON.stringify(values, 0, 2)}`);
    setIsLoading(true);
    // await sleep(1500);
    // calling update profile API
    const doctorData = {
      firstName: values.firstName,
      lastName: values.lastName,
      phone: values.phone,
      title: values.title,
      specialityId: values.specialityId,
      image: file,
      addresses: [
        {
          city: values.city,
          streetAddress: values.streetAddress,
          name: "",
          mapLocation: "",
        },
      ],
    };
    let response;
    if (props.mode === PageMode.Update) {
      if (!props.selectedDoctor.email) doctorData.email = values.email;
      response = await DoctorService.updateDoctor(
        props.selectedDoctor?.id,
        doctorData
      );
    } else {
      doctorData.password =
        doctorData.firstName.toLowerCase() + doctorData.lastName.toLowerCase();
      doctorData.email = values.email;
      response = await DoctorService.createDoctor(doctorData);
    }
    setIsLoading(false);
    if (!!!response.success && !response.success) {
      setMessage(!!response.message && response.message);
      return;
    }
    props.onClose(response.body);
  };

  const validate = async (values) => {
    const errors = {};
    if (!values.firstName) {
      errors.firstName = required;
    }
    if (!values.lastName) {
      errors.lastName = required;
    }
    if (!values.email) {
      errors.email = required;
    } else if (
      !values.email
        .toLowerCase()
        .match(
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        )
    ) {
      errors.email = "Invalid Email format!";
    }
    if (!values.phone) {
      errors.phone = required;
    }
    if (!values.title) {
      errors.title = required;
    }
    if (!values.specialityId) {
      errors.specialityId = required;
    }
    if (!values.image) {
      errors.image = required;
    }
    if (!values.city) {
      errors.city = required;
    }
    if (!values.streetAddress) {
      errors.streetAddress = required;
    }
    return errors;
  };
  const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);

      fileReader.onload = () => {
        resolve(fileReader.result);
      };

      fileReader.onerror = (error) => {
        reject(error);
      };
    });
  };
  const uploadImage = async (e) => {
    const file = e.target.files[0];
    const base64 = await convertBase64(file);
    setFile(base64);
  };

  let doctorForm = (
    <Form
      validate={validate}
      onSubmit={onSubmit}
      initialValues={{
        title: props.selectedDoctor?.title,
        firstName: props.selectedDoctor?.firstName,
        lastName: props.selectedDoctor?.lastName,
        email: props.selectedDoctor?.email,
        phone: props.selectedDoctor?.phone,
        specialityId: props.selectedDoctor?.specialityId,
        image: props.selectedDoctor?.image,
        streetAddress: props.selectedDoctor?.addresses[0]?.streetAddress,
        city: props.selectedDoctor?.addresses[0]?.city,
      }}
      render={({
        handleSubmit,
        onChange,
        submitError,
        form,
        submitting,
        pristine,
        values,
      }) => (
        <form onSubmit={handleSubmit} className="needs-validation" noValidate>
          {message && (
            <div className="invalid-feedback d-block mb-2">{message}</div>
          )}
          <h4 className="card-title">Doctor Information:</h4>
          <div className="row form-row">
            <div className="col-12 col-md-6">
              <div className="form-group">
                <label>Title</label>
                <Field
                  name="title"
                  className="form-control floating"
                  type="text"
                  component={InputField}
                />
              </div>
            </div>
            <div className="col-12 col-md-6">
              <div className="form-group">
                <label>Speciality</label>
                <Field name="specialityId" showerrorbeside="true">
                  {({ input, meta }) => {
                    return (
                      <select
                        className="form-control select"
                        defaultValue={values.specialityId}
                        value={values.specialityId}
                        {...input.select}
                        onChange={(e) => {
                          input.onChange(e); //final-form's onChange
                        }}
                      >
                        <option value="">
                          {loading ? "loading ..." : "Select Your Speciality"}
                        </option>
                        {specialitiesData?.map((val, index) => (
                          <option key={index} value={val.id}>
                            {val.name}
                          </option>
                        ))}
                      </select>
                    );
                  }}
                </Field>
                <Error name={"specialityId"} showerrorbeside="true" />
              </div>
            </div>
            <div className="col-12 col-md-6">
              <div className="form-group">
                <label>First Name</label>
                <Field
                  name="firstName"
                  className="form-control floating"
                  type="text"
                  component={InputField}
                />
              </div>
            </div>
            <div className="col-12 col-md-6">
              <div className="form-group">
                <label>Last Name</label>
                <Field
                  name="lastName"
                  className="form-control floating"
                  type="text"
                  component={InputField}
                />
              </div>
            </div>
            <div className="col-12 col-md-6">
              <div className="form-group">
                <label>Email ID</label>
                <Field
                  name="email"
                  className="form-control floating"
                  type="email"
                  component={InputField}
                />
              </div>
            </div>
            <div className="col-12 col-md-6">
              <div className="form-group">
                <label>Mobile</label>
                <Field
                  name="phone"
                  className="form-control floating"
                  type="text"
                  component={InputField}
                />
              </div>
            </div>
            <div className="col-12 col-md-6">
              <div className="form-group">
                <label>Image</label>
                <Field name="image" showerrorbeside="true">
                  {({ input: { value, onChange, ...input } }) => (
                    <input
                      {...input}
                      type="file"
                      className="form-control floating"
                      onChange={(e) => {
                        onChange(e);
                        uploadImage(e);
                      }}
                    />
                  )}
                </Field>
                <Error name={"image"} showerrorbeside="true" />
              </div>
            </div>
          </div>
          <h4 className="card-title">Address details:</h4>
          <div className="row form-row">
            <div className="col-12 col-md-6">
              <div className="form-group">
                <label>City</label>
                <Field
                  name="city"
                  className="form-control floating"
                  type="text"
                  component={InputField}
                />
              </div>
            </div>
            <div className="col-12 col-md-6">
              <div className="form-group">
                <label>Street Address</label>
                <Field
                  name="streetAddress"
                  className="form-control floating"
                  type="text"
                  component={InputField}
                />
              </div>
            </div>
          </div>
          <div className="submit-section">
            <button
              type="submit"
              className="btn btn-primary submit-btn"
              disabled={isLoading}
            >
              {!isLoading ? `Save` : ""}
              {isLoading && (
                <span className="spinner-border spinner-border-sm mb-1"></span>
              )}
            </button>
            <button
              className="btn btn-secondary submit-btn"
              onClick={() => {
                props.onClose(null);
              }}
            >
              Close
            </button>
          </div>
        </form>
      )}
    />
  );

  return <div>{doctorForm}</div>;
}
export default AddUpdateForm;
